function getSkinTexture(skin) {
  
    return SkinRegistry[skin]
}

  function Effect(str) {
    return str
  }
  function Buyable(name, description, icon, price, bobuxPerSec) {
    return {
      name: name,
      description: description,
      icon: icon,
      price: price,
      bobuxPerSec: bobuxPerSec
        
    }
  }

  function Opto(name, description, stats, icon, price, target, effect, unlockArray) {
    return {
      name: name,
      description: description,
      stats: stats,
      icon: icon,
      price: price,
      target: target,
      effect: effect,
      unlockArray: unlockArray,
 
    }
  }

  function OptoSkin(name, description, stats, icon, price, target, effect, unlockArray, skin) {
    return {
      skin: true,
      name: name,
      description: description,
      stats: stats,
      icon: icon,
      price: price,
      target: target,
      effect: effect,
      unlockArray: unlockArray,
      
    }
  }
  export {Buyables, Optos, optoEffects, getSkinTexture}