const useUnload = (fn, depen) => {
  const cb = React.useRef(fn);
  
  const unloadParams = () => {
      cb.current(depen[0], depen[1]);
  }
  React.useEffect(() => {
    
    window.addEventListener('beforeunload', unloadParams);
    return () => {
      window.removeEventListener('beforeunload', unloadParams);
    };
  }, [cb].concat(depen));
};

const useUnfocus = (fn) => {
  const cb = React.useRef(fn);
  
  const unfocusParams = () => {
      cb.current();
  }
  React.useEffect(() => {
    
    window.addEventListener('blur', unfocusParams);
    return () => {
      window.removeEventListener('blur', unfocusParams);
    };
  }, [cb]);
};

const useFocus = (fn, depen) => {
  const cb = React.useRef(fn);
  
  const focusParams = () => {
   
      cb.current(depen[0], depen[1]);
  }
  React.useEffect(() => {
    
    window.addEventListener('focus', focusParams);
    return () => {
      window.removeEventListener('focus', focusParams);
    };
  }, [cb].concat(depen));
};



