import { Optos } from "./Buyables";
import { nanoid } from "nanoid";
import { useState } from "react"; 
import Opto from "./Opto";

function OptoUpgrades(props) {
    let game = props.game[0]
    let setGame = props.game[1]
    let setBobux = props.bobux.setBobux
    let bobux = props.bobux.bobux
    let [red, setRed] = useState("")
    const isExist = (game.optos == undefined)
    
    
    const onBuy = (opto, key) => {
        
        if (bobux < opto.price) {
            setRed("red")
            setTimeout(() => {setRed("")}, 100)
            return
        }
        if (opto.skin == true) {
            props.addSkin(opto, key)
        }
       
        game.optos = game.optos == undefined ? {} : game.optos
        game.optos[opto.name] = opto
        
        if (game.buyables[opto.target].effects == undefined) {game.buyables[opto.target].effects = [opto.effect]} else {
            game.buyables[opto.target].effects = [...game.buyables[opto.target].effects, opto.effect]
        }
        setBobux(bobux - opto.price)
        setGame(game)
    }
    const arrayDeOptos = Object.keys(Optos)
    let updatedArray = arrayDeOptos.map(key => {
        return {...Optos[key], key}
    })


    let upgrades = updatedArray.filter(opto => {
       
        if (game.buyables[opto.target] == undefined) {return false}
        if (isExist) {return true}
        if (game.optos[opto.name] == undefined) {
            return true
        }
        
        return false
    })
    
    if (upgrades.length == 0) {
        
        return null
    }
   
    return (
        <div className="OptoUpgrades" id={red}>
            {upgrades.map(opto => {
                
                return (
                    <Opto key={"opto-" + nanoid()} opto={opto} onBuy={onBuy} keey={opto.key}/>
                )
            })}
        </div>
    )
}

export default OptoUpgrades;