
function Opto(props) {
    const key = props.keey
    let onBuy = () => {
        
        props.onBuy(props.opto, key)
    }
    
    return (
        <div className="Opto" onTouchStart={onBuy} onPointerDown={onBuy} onClick={onBuy}>
            <div className="Opto-icon" onClick={onBuy}>
                 <img src={props.opto.icon}></img>
            </div>
            <div className="Opto-info" onClick={onBuy}>
                <p className="Opto-name">{props.opto.name}</p>
                <p className="Opto-price">{props.opto.price}฿</p>
                <p className="Opto-stats">{props.opto.stats}</p>
                <p className="Opto-desc">{props.opto.description}</p>
                
            </div>
        </div>

    )
}
export default Opto;