import { useState } from "react";
import { nanoid } from "nanoid";
import { getSkinTexture } from "./Buyables";
function VisualRow(props) {
    let game = props.game
    let key = props.keey
  
    let imgs = []
    let amountRemaining = props.amount
    if (game.skins != undefined) {
     
        if (game.skins[key] != undefined) {
           
            let skins = game.skins[key]
            
            Object.values(skins).forEach(skin => {
                amountRemaining -= skin.amount
               
                imgs.push(<img src={getSkinTexture(skin.texture)} key={"icon-" + nanoid()} alt={props.name + "with a " + skin.name + " skin"}></img>)
            })
        }        
    }
    
    
    for (let i = 0; i < amountRemaining; i++) { 
        imgs.push(<img src={props.icon} key={"icon-" + nanoid()} alt={props.name}></img>)
    }
    return (
        <div className="VisualRow">
            {imgs}
        </div>
    )
}
export default VisualRow;