import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import Cookies from 'js-cookie';


function isJsonString(str) {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}
let saveData = Cookies.get('saveData');
console.log(saveData)
document.title = "Bobux Generator"

const setSaveData = (data) => {
 
  Cookies.set('saveData', JSON.stringify(data), { expires: 9999 });
  saveData = data;
}

if (isJsonString(saveData) == false) {
  console.log("No save data found, creating new save data.")
  setSaveData(JSON.stringify({buyables:{}, bobux:0}))
}
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App saveData={saveData} setSaveData={setSaveData}/>
  </React.StrictMode>
);

